$font-stack: 'GothamPro', sans-serif;

@font-face {
  font-family: 'GothamPro';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro.ttf);
}

@font-face {
  font-family: 'GothamPro-Light';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Light.ttf);
}

@font-face {
  font-family: 'GothamPro-Black';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Black.ttf);
}

@font-face {
  font-family: 'GothamPro-Medium';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Medium.ttf);
}

$font : 'GothamPro';
$fontLight : 'GothamPro-Light';
$fontBlack : 'GothamPro-Black';
$fontMedium : 'GothamPro-Medium';


.none {
  padding: 0;
  margin: 0;
}

html {
  font-size: 18px;
}

body {
  font-family: $font-stack;
  font-size: 18px;
  color: #383838;
}

.menuBlock {
  width: 50%;
  min-height: 350px;
  height: 100%;
  max-height: 1100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9000;
  overflow: hidden;

  #menuHumburger {
    width:25px;
    height:25px;
    background-image:url(../img/sprite_x25.png);
    background-position:0% 0%;
    cursor: pointer;
    display: inline-block;
    position: absolute;
    top: 2.5vw;
    right: 57%;
    z-index: 9999;
  }

  .backgroundCircle {
      background-color: #c8ae76;
      width: 25px;
      height: 25px;
      border-radius: 2500px;
      right: 57%;
      position: absolute;
      top: 33px;
      display: none;
  }
  ul {
    display: none;
    position: absolute;
    letter-spacing: 0.03rem;
    z-index: 9999;
    right: 12%;
    top: 14.2vw;
    font-size: 1vw;
    list-style: none;
    color: #fff;
    font-family: $fontMedium;
    margin: 0;
    padding: 0;
    width: 39%;
    li {
      margin-bottom: 0.79vw;
      a {
        color: #fff;
        &:hover {
          color: #fff;
        }
      }
    }
    #separator {
      background-color: #e1e1e1;
      height: 1px;
      width: 100%;
      margin-top: 4.2vw;
      margin-bottom: 1.7vw;
    }
  }
}

header {
  position: relative;
  overflow: hidden;
  height: 56.3vw;
  h1 {
    position: absolute;
    top: 2.2vw;
    left: 8.5%;
    letter-spacing: 0.04rem;
    font-size: 2vw;
    font-family: GothamPro-Light;
  }

  h2 {
    position: absolute;
    top: 22vw;
    left: 9%;
    letter-spacing: 0.3rem;
    font-size: 3vw;
    text-transform: uppercase;
    span {
      display: block;
      &:first-child {
        font-family: $fontLight;
      }
      &:last-child {
        font-family: $font;
        letter-spacing: 0.4rem;
      }
    }
  }
  img {
    position: absolute;
    top: 0;
    /*left: 65px;
    width: 555px;*/

    left: 8.8vw;
    width: 40.6vw;
    z-index: -9999;
  }

  .socialBlock {
    position: absolute;
    top: 2.5vw;
    left: 57%;
    height: auto;
    z-index: 1;
    width: auto;
    a {
      img {
      width: 1.8vw;
      height: auto;
      z-index: 0;
      position: relative;
      left: 0;
      }
    }
  }
  .arrowNext {
    position: absolute;
    z-index: 9999;
    bottom: 1.7vw;
    margin-left: -1.5%;
    left: 0;
    height: auto;
    width: 3%;
    img {
      width: 100%;
      position: relative;
      z-index: 9999;
      left: 0;
    }
  }
  .row, .col-sm-6 {
    height: 100%;
  }

  .buttonAdditional {
    position: absolute;
    top: 33.4vw;
    left: 9%;
  }
}

.screenOne, .screenThree {
  background-image: url(../img/W.png);
  background-repeat: no-repeat;
  background-position: 77.5% 6vw;
  background-size: 38%;
  overflow: hidden;
  img {
    width: 47.1%;
    margin-left: 9%;
    margin-top: -4.3vw;
  }
  .titleScreen {
    text-transform: uppercase;
    color: #000;
    font-family: GothamPro;
    font-size: 1.3vw;
    margin: 4.2vw 0 0 18.7%;
    letter-spacing: .08vw;
  }
  .bodyScreen {
    margin: .7vw 0 2.9vw 18.7%;
    font-size: 0.79vw;
    display: block;
    width: 30%;
    line-height: 1.3vw;
    letter-spacing: 0.045vw;
  }
  .separator {
    height: 1px;
    background-color: #b99e65;
    display: inline-block;
    width: 7.5%;
    margin: 1.3vw 0 0 18.7%;
  }
  .buttonAdditional {
    width: 14.5%;
    text-align: center;
    margin: 2.6vw 0 0 18.5%;
    font-size: .75vw;
    padding: .5vw 0 .5vw;
    text-transform: unset;
  }
}

.screenTwo, .screenFour {
  background-image: url(../img/П.png);
  background-repeat: no-repeat;
  background-position: 12.5% 8vw;
  background-size: 21%;
  overflow: hidden;
  img {
    width: 44.1%;
    margin-left: 28%;
    margin-top: -2.1vw;
  }
  .titleScreen {
    text-transform: uppercase;
    color: #000;
    font-family: GothamPro;
    font-size: 1.3vw;
    margin: 4.2vw 0 0 34.7%;
    letter-spacing: .08vw;
  }
  .bodyScreen {
    margin: .7vw 0 2.9vw 34.7%;
    font-size: 0.79vw;
    display: block;
    width: 30%;
    line-height: 1.3vw;
    letter-spacing: 0.045vw;
  }
  .separator {
    height: 1px;
    background-color: #b99e65;
    display: inline-block;
    width: 7.5%;
    margin: 1.3vw 0 0 34.7%;
  }
  .buttonAdditional {
    width: 14.5%;
    text-align: center;
    margin: 2.6vw 0 0 34.8%;
    font-size: .75vw;
    padding: .5vw 0 .5vw;
    text-transform: unset;
  }
}

.screenThree {
  background-image: url(../img/O.png);
  background-repeat: no-repeat;
  background-position: 68% 10.5vw;
  background-size: 27%;
}

.screenFour {
  background-image: url(../img/Т.png);
  background-repeat: no-repeat;
  background-position: 9.5% 8vw;
  background-size: 21%;
}

.button {
  letter-spacing: 0.089rem;
  font-size: 0.74vw;
  border: 1px solid #000;
  text-transform: uppercase;
  padding: 0.8vw 7.5% 0.8vw 8.3%;
  display: block;
  color: #000;
    &:hover, &:active {
      color: #000;
    }
}

.pagesIndex {
  width: 22%;
  position: fixed;
  right: 0;
  top: 15vw;
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    li {
      margin-bottom: 1vw;
      margin-top: 1.5vw;
      margin-left: 5.4%;
      &:first-child {
          margin-top: 0;
      }
      a {
        color: #7d7d7d;
        &:hover, &:active {
          color: #7d7d7d;
          text-decoration: none;
        }
        p {
          display: inline-block;
          font-size: 1.5vw;
          margin: 0;
        }
        span {
          width: 79%;
          height: 2px;
          background-color: #444;
          display: none;
          vertical-align: top;
          margin-top: 1.7vw;
          margin-left: 7.5%;

        }
      }
    }
    .active {
      margin-left: 0;
      a {
        color: #444;
        &:hover, &:active {
          color: #444;
          text-decoration: none;
        }
      }
      p {
      font-size: 2vw;
      }
      span {
        display: inline-block;
      }
    }
  }
}


footer {
  height: 19.8vw!important;
  padding: 2.95vw 0 0 0;
  background-color: #f0f0f0;
  .container {
    padding: 0;
  }
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
      font-size: 1.1vw;
      letter-spacing: 0.05vw;
      margin-bottom: 0.84vw;
      a, a:hover {
        color: #000;
      }
    }
  }
  .col-sm-3 {
    &:nth-child(2) > ul {
      padding-left: 2.5%;
    }
    &:nth-child(3) {
      p {
        font-size: 1.2vw;
        margin: -0.15vw 0 3.2vw 5%;
        padding: 0;
        display: block;
        color: #000;
      }
      div {
        p {
          font-size: 1.2vw;
          margin: 0 0 0.9vw 4%;
          letter-spacing: 0.02vw;
        }
        a {
          font-size: 1.2vw;
          margin: -0.15vw 0 0 5%;
          padding: 0;
          display: block;
          color: #000;
        }
      }
    }
    &:nth-child(4) {
      ul {
        margin-left: 7%;
        margin-top: 0.3vw;
        margin-bottom: 4vw;
        width: 20vw;
        li {
          font-size: 1.1vw;
          letter-spacing: 0.05vw;
          margin-bottom: 0.84vw;
          display: inline-block;
          margin-right: 17%;
          &:last-child {
            margin-right: 0;
            margin-left: 3%;
          }
          a {
            display: inline-block;
            img {
              width: 2.1vw;
            }
          }
          &:nth-child(2) > a > img {
              width: 2.5vw;
          }
          &:nth-child(3) > a > img {
              width: 1.2vw;
              margin-left: 0.1vw;
          }
          &:last-child > a > img {
              width: 2vw;
          }
        }
      }
      span {
        font-size: 1.2vw;
        display: block;
        margin: -.2vw 0 0 1.5vw;
        letter-spacing: -0.015vw;
        width: 20vw;
      }
    }
  }
  .bottomFooter {
    height: 4.88vw;
    background-color: #383838;
    margin: 2.1vw 0 0 0;
    .col-sm-6 > p {
      color: #f0f0f0;
      font-size: .95vw;
      margin: 1.1vw 0 0;
      line-height: 1.2vw;
      letter-spacing: 0.045vw;
    }
    .col-sm-3 {
      p {
        color: #f0f0f0;
        font-size: .95vw;
        margin: 2vw 0 0 6%;
        line-height: 1.2vw;
        letter-spacing: 0.045vw;
        width: 20vw;
        a, a:hover {
          color: #f0f0f0;
          display: inline-block;
        }
      }
      &:last-child {
        p {
          margin-left: 8%;
        }
        a {
          color: #f0f0f0;
          font-size: .95vw;
          display: inline-block;
          margin-left: 0;
        }
      }
    }
  }
}
